@import '../../../utils/colors';
@import '../../../utils/funcs';

.lists-holder {
  display: flex;
  align-items: center;
  justify-content: center;

  .box {
    height: calc(100vh - 100px);
    width: 100%;
    background-color: $bgLight;
    border-radius: 20px;
    padding: 20px;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: unset !important;

    .top-side {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;

      .right {
        span {
          font-weight: 500;
          @include fontSize(18);
          color: $blueColor;
        }
      }
    }

    form {
      margin-top: 30px;

      .datas {
        display: flex;
        align-items: flex-start;
        column-gap: 30px;
        flex-wrap: wrap;

        .form-group {
          margin-bottom: 30px;

          textarea {
            max-height: 165px;
            width: 400px;
            height: 150px;
          }
        }
      }

    }
  }
}