@import "./colors";
@import "./funcs";

.btn {
  padding: 10px 12px;
  border-radius: 15px;
  cursor: pointer;
  border: 2px solid transparent;
  display: flex;
  align-items: center;

  &:hover {

    i, ion-icon {
      transform: translateX(2px);
    }
  }

  i, ion-icon {
    margin-left: 10px;
  }

  span {
    @include fontSize(12);
    color: #fff !important;
  }

  i {
    @include fontSize(18);
  }

  &.primary-btn {
    background-color: $redColor;

    &.disabled {
      background-color: rgba(248, 68, 68, 0.66);
      cursor: not-allowed;

      &:hover {
        background-color: rgba(248, 68, 68, 0.66);

        i, ion-icon, span {
          color: #fff !important;
        }
      }
    }

    ion-icon, i {
      color: #fff;
    }

    &:hover {
      background-color: transparent;
      border-color: $redColor;

      i, ion-icon, span {
        color: $redColor !important;
      }
    }
  }

  &.blue-btn {
    background-color: $blueColor;

    ion-icon, i {
      color: #fff;
    }

    &:hover {
      background-color: transparent;
      border-color: $blueColor;

      i, ion-icon, span {
        color: $blueColor !important;
      }
    }

    &.blank {
      background-color: transparent;
      border-color: $blueColor;

      span, i {
        color: $blueColor !important;
      }

      &:hover {
        background-color: $blueColor;

        span, i {
          color: #fff !important;
        }
      }
    }

    &.disabled {
      background-color: #02789b;
      cursor: not-allowed;

      &:hover {
        background-color: #02789b;

        i, ion-icon, span {
          color: #fff !important;
        }
      }
    }

  }

  &.yellow-btn {
    background-color: $yellowColor;

    &:hover {
      background-color: transparent;
      border-color: $yellowColor;

      span {
        color: $yellowColor !important;

      }
    }
  }

  &.transparent {
    background-color: transparent;

    span {
      color: $darkText !important;
    }

    &:hover {
      border-color: #222;

      span {
        color: #222 !important;
      }
    }
  }

  &.green-btn {
    background-color: $greenColor;

    ion-icon, i {
      color: #fff;
    }

    &:hover {
      background-color: transparent;
      border-color: $greenColor;

      i, ion-icon, span {
        color: $greenColor !important;
      }
    }

    &.blank {
      background-color: transparent;
      border-color: $greenColor;

      span, i {
        color: $greenColor !important;
      }

      &:hover {
        background-color: $greenColor;

        span, i {
          color: #fff !important;
        }
      }
    }

    &.disabled {
      background-color: #036433;
      cursor: not-allowed;

      &:hover {
        background-color: #036433;

        i, ion-icon, span {
          color: #fff !important;
        }
      }
    }

  }
}