@import '../../../utils/colors.scss';
@import '../../../utils/funcs.scss';

.movie-content {
  overflow-y: auto !important;

  .section {
    width: 100%;
  }

  .movie-holder {
    padding-right: 50px;
    width: 100%;
    padding-top: 30px;

    #details-holder {
      padding: 20px 0;
      height: 100vh;
      margin-top: -60px;
    }

  }
}

//Responsive
@media screen and (max-width: 800px) {
  .movie-content {
    width: 100%;
    padding: 0 3%;
    margin-bottom: 0;
    overflow-y: unset !important;

    .movie-holder {
      width: 100%;
      padding: 0;
    }

  }
}