@import "./funcs";
@import "./colors";

.form-group {
  display: flex;
  align-items: flex-start;
  flex-direction: column;

  label {
    @include fontSize(12);
    color: #777;
    display: flex;
    align-items: center;

    span {
      @include fontSize(12);
      color: #777;
    }

    i {
      @include fontSize(14);
      color: #777;
      margin-right: 7px;
    }
  }

  input, textarea, select {
    margin-top: 10px;
    border: 1px solid #777;
    border-radius: 5px;
    padding: 10px 20px;
    color: #444;
    @include fontSize(14);

    &:focus {
      color: $blueColor;
      border-color: $blueColor;
    }
  }
}