@import '../../utils/colors';
@import '../../utils/funcs';

.modal {
  z-index: 100;
  position: fixed;
  height: 100vh;
  width: 100%;
  right: 0;
  top: 0;
  background-color: rgba(255, 255, 255, 0.6);
  align-items: center;
  display: none;


  &.active {
    display: flex;
  }

  & > div {
    flex: 1.5;
  }

  .modal-box {
    flex: 4.5;
    width: 100%;
    height: 70%;
    display: flex;
    align-items: center;
    justify-content: center;

    .inner {
      width: 50%;
      height: 100%;
      background-color: $bgColor;
      border-radius: 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      position: relative;

      .exit-btn {
        position: absolute;
        right: 20px;
        top: 20px;
        z-index: 110;
        cursor: pointer;

        i {
          color: $redColor;
          @include fontSize(30)
        }
      }

      form {
        display: flex;
        align-items: center;
        flex-direction: column;

        .file-result {
          display: flex;
          align-items: flex-start;
          flex-direction: column;
          margin-top: 20px;
          li{
            color: $blueColor;
          }
        }
      }

      .results {
        width: 100%;
        display: flex;
        align-items: flex-end;
        flex-direction: column;

        li {
          display: flex;
          align-items: center;
          justify-content: space-between;
          width: 90%;
          padding: 10px;
          border-radius: 5px;
          background-color: $bgLight;
          border: 2px solid $blueColor;

          margin: 0 auto;
          margin-bottom: 40px;

          .title {
            color: $blueColor;
            max-width: 300px;
            overflow: hidden;
            text-overflow: ellipsis;
          }

          .value {
            color: $greenColor;

            &.red {
              color: $redColor !important;
            }
          }
        }
      }
    }

  }

}