@import "./utils/colors";
@import "./utils/funcs";
@import "./utils/forms";

*::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

*::-webkit-scrollbar-track {
  background: #ccc;
}

*::-webkit-scrollbar-thumb {
  background: #787878;

  &:hover {
    background: #555;
  }
}

* {
  scroll-behavior: smooth;
  padding: 0;
  margin: 0;
  transition: all 0.2s ease;
  box-sizing: border-box;
  color: $textColor;
  font-family: 'IRANSans', sans-serif;
  font-size: 1rem;
  direction: rtl;
}

body, html {
  width: 100vw;
  background-color: $bgColor;
  height: 100vh;

}

.en {
  font-family: 'Poppins', sans-serif;
  direction: ltr !important;
}

.root {
  width: 100%;
  height: 100%;
}

a, button {
  cursor: pointer;
}

button, input, textarea, select {
  outline: none;
  border: none;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

@for $i from 1 through 6 {
  h#{$i} {
    font-weight: normal;
  }
}

.circular-btn {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: grid;
  place-items: center;
  background-color: $bgLight;
  border: 2px solid transparent;

  &:hover {
    border-color: #fff;

    i {
      color: #fff;
    }
  }

  i {
    @include fontSize(16);
    color: $lightText;
  }
}

.flex {
  display: flex;
}

.align-center {
  align-items: center;
}

.justify-between {
  justify-content: space-between;
}

.justify-center {
  justify-content: center;
}

.yellow-text {
  color: $yellowColor !important;
}

.loader {
  width: 48px;
  height: 48px;
  border: 3px solid $yellowColor;
  border-radius: 50%;
  display: inline-block;
  position: relative;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;

  &::after {
    content: '';
    box-sizing: border-box;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 56px;
    height: 56px;
    border-radius: 50%;
    border: 3px solid;
    border-color: $blueColor transparent;
  }
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.splitter {
  height: 20px;
  width: 2px;
  background-color: $darkText;
  border-radius: 25px;
}

.labeled-item {
  border-radius: 5px;
  padding: 5px 10px;
  background-color: $bgColor;
  text-align: center;
  @include fontSize(12);
  color: $lightText;

  &:hover {
    color: $textColor;
  }
}

.progress-indicator {
  height: 5px;
  background-color: $bgLight;
  border-radius: 25px;
  position: relative;
  flex: 12;
  margin: 0 10px;
  z-index: 2;
  overflow: visible;

  &.small {
    flex: 10;
  }

  input {
    width: 100%;
    position: absolute;
    height: 100%;
    left: 0;
    right: 0;
    z-index: 4;
    appearance: none;
    cursor: pointer;
    background-color: transparent;

    &::-webkit-slider-thumb {
      -webkit-appearance: none;
      appearance: none;
      width: 10px;
      height: 10px;
      border-radius: 50%;
      background: $blueColor;
      cursor: pointer
    }
  }

  .passed {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    background-color: $blueColor;
    border-radius: 25px;
    z-index: 3;
    transition: all 100ms ease;
  }
}

.content {
  display: flex;
  align-items: flex-start;
  height: 100vh;
  overflow: hidden;

  .holder {
    flex: 4.5;
    display: flex;
    align-items: flex-start;
    padding: 0 3%;
    overflow-y: auto;
    flex-direction: column;
    margin-left: 20px;
    height: 100%;
    overflow-x: hidden;
    padding-left: calc(3% + 15px);
  }

}