@import '../../../utils/colors';
@import '../../../utils/funcs';

.lists-holder {
  display: flex;
  align-items: center;
  justify-content: center;

  .box {
    height: calc(100vh - 100px);
    width: 100%;
    background-color: $bgLight;
    border-radius: 20px;
    padding: 20px;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: space-between;

    overflow-y: auto;

    .top-side {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;

      .right {
        span {
          font-weight: 500;
          @include fontSize(18);
          color: $blueColor;
        }
      }
    }

    form {
      display: flex;
      align-items: flex-start;
      column-gap: 30px;
      flex-wrap: wrap;

      .form-group {
        margin-bottom: 30px;

        textarea {
          max-height: 165px;
          max-width: 400px;
        }
      }
    }

    .lists-table {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      flex-direction: column;
      justify-content: center;
      padding-top: 40px;

      .titles {
        width: 100%;
        display: flex;
        align-items: center;

        li {
          color: $blueColor;
          width: 25%;
          text-align: center;
          @include fontSize(14);
        }
      }

      .lists {
        width: 100%;
        height: 100%;
        padding: 20px;
        display: flex;
        align-items: flex-start;
        flex-direction: column;

        li {
          width: 100%;

          display: flex;
          align-items: center;
          border: 2px solid transparent;
          padding-bottom: 10px;
          border-bottom-color: $bgColor;

          & > div {
            width: 25%;
            text-align: center;
            @include fontSize(14);
            color: $yellowColor;

            a {
              text-align: center;
              @include fontSize(14);
              color: $yellowColor;

              &:hover {
                color: $blueColor;
              }
            }

            &.funcs {
              display: flex;
              align-items: center;
              justify-content: center;

              button {
                margin-right: 10px;

                &:first-child {
                  margin-right: 0;
                }

                width: 20px;
                height: 20px;
                border-radius: 5px;
                display: grid;
                place-items: center;
                padding: 0;

                i {
                  margin-left: 0;
                  @include fontSize(12);
                }

                &:hover {
                  i {
                    transform: unset;
                  }
                }
              }
            }
          }

        }
      }
    }
  }

}