@import "../../../utils/funcs";
@import "../../../utils/colors";

.functions {
  height: 100vh;
  margin-top: 30px;

  .func-box {
    background-color: $bgLight;
    border-radius: 25px;
    padding: 20px 20px 20px 35px;
    overflow-y: auto;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    min-height: 500px;

    .func-list {
      display: flex;
      align-items: flex-start;
      flex-direction: column;

      .list-title {
        @include fontSize(16);
        color: $lightText;
        font-weight: 500;
      }

      .items {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-top: 20px;

        li {
          margin-right: 20px;

          &:first-child {
            margin-right: 0;
          }

          button {
            padding: 7px 10px;
            border-radius: 5px;
            display: flex;
            align-items: center;
            background-color: $blueColor;

            &:hover {
              transform: scale(1.05);
            }

            span {
              @include fontSize(14);
              color: #fff;
              margin-right: 7px;
            }

            i, ion-icon {
              @include fontSize(16);
              color: #fff;
            }
          }
        }
      }
    }
  }
}
